import axios from "axios";
import { Component } from "react";
import { NavLink, Route } from "react-router-dom";
import AuthorBooks from "../components/AuthorBooks";

class AuthorViews extends Component {
  state = {
    authors: [],
  };

  async componentDidMount() {
    const response = await axios.get(
      ` http://localhost:4040/authors?_embed=books`
    );
    this.setState({ authors: response.data });
  }

  render() {
    return (
      <>
        <h1>Authors</h1>
        <ul>
          {this.state.authors.map(({ name, id }) => (
            <li key={id}>
              <NavLink to={`${this.props.match.url}/${id}`}>{name}</NavLink>
            </li>
          ))}
        </ul>

        <Route
          path={`${this.props.match.path}/:authorId`}
          render={(props) => {
            const bookId = Number(props.match.params.authorId);

            const author = this.state.authors.find(({ id }) => id === bookId);

            return author && <AuthorBooks {...props} authors={author.books} />;
          }}
        />
      </>
    );
  }
}

export default AuthorViews;
