import { Route, NavLink, Switch } from "react-router-dom";
import HomeViews from "./HomeViews";
import AuthorViews from "./AuthorViews";
import BooksViews from "./BooksViews";
import NotFoundView from "./NotFoundView";
import BookDetailsView from "./BookDetailsView";

// const linkStyles = {
//   base: {
//     color: "black",
//   },
//   active: {
//     color: "red",
//   },
// };

const App = () => (
  <>
    <ul>
      <li>
        <NavLink
          exact
          to="/"
          className="NavLink"
          activeClassName="NavLink--active"
        >
          Home
        </NavLink>
      </li>
      <li>
        <NavLink
          to="/authors"
          className="NavLink"
          activeClassName="NavLink--active"
        >
          Authors
        </NavLink>
      </li>
      <li>
        <NavLink
          to="/books"
          className="NavLink"
          activeClassName="NavLink--active"
        >
          Books
        </NavLink>
      </li>
    </ul>
    <Switch>
      <Route exact path="/" component={HomeViews} />
      <Route path="/authors" component={AuthorViews} />
      <Route exact path="/books" component={BooksViews} />
      <Route path="/books/:booksId" component={BookDetailsView} />
      <Route component={NotFoundView} />
    </Switch>
  </>
);

export default App;
